

























import { Component, Vue } from "vue-property-decorator";
import { Nullable } from "@/types/nullable";
import api from "@/data/apis";
import console from "@/util/console";

@Component
export default class SubscribeNewsletter extends Vue {
  email: Nullable<string> = null;

  async submit(): Promise<void> {
    console.info("subscribe -newsletter", this.email);
    if (this.email) {
      await api.subscribeNewsletter(this.email).then((result) => {
        console.log("comments - saved", result);
      });
      this.$router.push("/");
    }
  }
}
